"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Dialog } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select } from "@/components/ui/select"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"

interface Product {
  id: string
  name: string
  size: string
  color: string
  quantity: number
  costPrice: number
  sellingPrice: number
  profit: number
}

export default function Home() {
  const [products, setProducts] = useState<Product[]>([])
  const [isBuyModalOpen, setIsBuyModalOpen] = useState(false)
  const [isSellModalOpen, setIsSellModalOpen] = useState(false)
  const [newProduct, setNewProduct] = useState({
    name: "",
    size: "",
    color: "",
    quantity: 0,
    costPrice: 0,
    sellingPrice: 0,
  })

  const calculateTotalStock = () => {
    return products.reduce((total, product) => total + product.quantity, 0)
  }

  const calculateTotalProfit = () => {
    return products.reduce((total, product) => total + product.profit, 0)
  }

  const handleBuySubmit = () => {
    const profit = (newProduct.sellingPrice - newProduct.costPrice) * newProduct.quantity
    const product: Product = {
      id: Date.now().toString(),
      ...newProduct,
      profit,
    }
    setProducts([...products, product])
    setIsBuyModalOpen(false)
    setNewProduct({
      name: "",
      size: "",
      color: "",
      quantity: 0,
      costPrice: 0,
      sellingPrice: 0,
    })
  }

  return (
    <main className="min-h-screen p-8">
      {/* Header */}
      <div className="mb-8 text-center">
        <h1 className="text-4xl font-bold mb-2">Campus Tailor</h1>
        <p className="text-gray-600">Inventory Management System</p>
      </div>

      {/* Summary Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-8">
        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-2">Total Stock</h2>
          <p className="text-3xl">{calculateTotalStock()}</p>
        </Card>
        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-2">Total Profit</h2>
          <p className="text-3xl">₹{calculateTotalProfit()}</p>
        </Card>
      </div>

      {/* Action Buttons */}
      <div className="flex gap-4 mb-8">
        <Button onClick={() => setIsBuyModalOpen(true)}>Buy Stock</Button>
        <Button onClick={() => setIsSellModalOpen(true)} variant="secondary">
          Sell Stock
        </Button>
      </div>

      {/* Inventory Table */}
      <div className="rounded-md border">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Product Name</TableHead>
              <TableHead>Size</TableHead>
              <TableHead>Color</TableHead>
              <TableHead>Quantity</TableHead>
              <TableHead>Cost Price</TableHead>
              <TableHead>Selling Price</TableHead>
              <TableHead>Profit</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {products.map((product) => (
              <TableRow key={product.id}>
                <TableCell>{product.name}</TableCell>
                <TableCell>{product.size}</TableCell>
                <TableCell>{product.color}</TableCell>
                <TableCell>{product.quantity}</TableCell>
                <TableCell>₹{product.costPrice}</TableCell>
                <TableCell>₹{product.sellingPrice}</TableCell>
                <TableCell>₹{product.profit}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>

      {/* Buy Modal */}
      <Dialog open={isBuyModalOpen} onOpenChange={setIsBuyModalOpen}>
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center">
          <div className="bg-white p-6 rounded-lg w-full max-w-md">
            <h2 className="text-2xl font-bold mb-4">Buy Stock</h2>
            <div className="space-y-4">
              <div>
                <Label htmlFor="name">Product Name</Label>
                <Input
                  id="name"
                  value={newProduct.name}
                  onChange={(e) =>
                    setNewProduct({ ...newProduct, name: e.target.value })
                  }
                />
              </div>
              <div>
                <Label htmlFor="size">Size</Label>
                <select
                  id="size"
                  className="w-full border rounded-md p-2"
                  value={newProduct.size}
                  onChange={(e) =>
                    setNewProduct({ ...newProduct, size: e.target.value })
                  }
                >
                  <option value="">Select Size</option>
                  <option value="S">S</option>
                  <option value="M">M</option>
                  <option value="L">L</option>
                  <option value="XL">XL</option>
                </select>
              </div>
              <div>
                <Label htmlFor="color">Color</Label>
                <Input
                  id="color"
                  value={newProduct.color}
                  onChange={(e) =>
                    setNewProduct({ ...newProduct, color: e.target.value })
                  }
                />
              </div>
              <div>
                <Label htmlFor="quantity">Quantity</Label>
                <Input
                  id="quantity"
                  type="number"
                  value={newProduct.quantity}
                  onChange={(e) =>
                    setNewProduct({
                      ...newProduct,
                      quantity: parseInt(e.target.value) || 0,
                    })
                  }
                />
              </div>
              <div>
                <Label htmlFor="costPrice">Cost Price</Label>
                <Input
                  id="costPrice"
                  type="number"
                  value={newProduct.costPrice}
                  onChange={(e) =>
                    setNewProduct({
                      ...newProduct,
                      costPrice: parseFloat(e.target.value) || 0,
                    })
                  }
                />
              </div>
              <div>
                <Label htmlFor="sellingPrice">Selling Price</Label>
                <Input
                  id="sellingPrice"
                  type="number"
                  value={newProduct.sellingPrice}
                  onChange={(e) =>
                    setNewProduct({
                      ...newProduct,
                      sellingPrice: parseFloat(e.target.value) || 0,
                    })
                  }
                />
              </div>
              <div className="flex justify-end gap-4">
                <Button variant="outline" onClick={() => setIsBuyModalOpen(false)}>
                  Cancel
                </Button>
                <Button onClick={handleBuySubmit}>Add Stock</Button>
              </div>
            </div>
          </div>
        </div>
      </Dialog>

      {/* Sell Modal */}
      <Dialog open={isSellModalOpen} onOpenChange={setIsSellModalOpen}>
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center">
          <div className="bg-white p-6 rounded-lg w-full max-w-md">
            <h2 className="text-2xl font-bold mb-4">Sell Stock</h2>
            <div className="space-y-4">
              <div>
                <Label htmlFor="sellProduct">Select Product</Label>
                <select className="w-full border rounded-md p-2">
                  <option value="">Choose a product</option>
                  {products.map((product) => (
                    <option key={product.id} value={product.id}>
                      {product.name} - {product.size} - {product.color}
                    </option>
                  ))}
                </select>
              </div>
              <div>
                <Label htmlFor="sellQuantity">Quantity</Label>
                <Input id="sellQuantity" type="number" />
              </div>
              <div className="flex justify-end gap-4">
                <Button variant="outline" onClick={() => setIsSellModalOpen(false)}>
                  Cancel
                </Button>
                <Button>Sell</Button>
              </div>
            </div>
          </div>
        </div>
      </Dialog>
    </main>
  )
}
